@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto&display=swap);
html{box-sizing:border-box;font-family:'Roboto', sans-serif}*,*:before,*:after{box-sizing:inherit}body{margin:0}.map{margin:50px auto 0 auto;position:relative}@media all and (max-width: 1239px){.map{overflow-x:scroll}}@media all and (min-width: 1100px){.map{margin:100px auto 0 auto}}.point{position:absolute;width:4px;height:4px;background-color:red;border-radius:50%}.point_wrapper{position:relative;width:100%;height:100%}.callOut{position:absolute;top:-75px;left:-150px;width:150px;height:70px;padding:5px;background-color:#fff;border:1px solid #eee;border-radius:5px;text-align:center;transform:translateX(50%);font-family:'Roboto', sans-serif;font-size:12px;font-weight:800;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.75)}.callOut_wrapper{position:relative;width:100%;height:100%}.callOut_country{letter-spacing:2px;font-weight:800;position:relative;z-index:2}.callOut_deaths{color:#FF1E0A}.callOut_recovered{color:#5CA34A;position:relative;z-index:2}.callOut span{font-weight:400;color:#000}.callOut_triangle{position:absolute;width:10px;height:10px;background-color:#fff;bottom:-10px;left:50%;transform:rotate(45deg) translateX(-50%);z-index:1}@media all and (max-width: 1239px){.bottom .callOut{top:inherit;bottom:-75px}.bottom .callOut_triangle{bottom:inherit;top:-4px}}.error{position:fixed;width:85%;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;background-color:rgba(159,58,56,0.5);font-size:20px;color:#fff;padding:5px 0;border-radius:5px;border:2px solid #5f1715}.error_btn{border:none;background:none;color:#fff;float:right;font-size:20px;cursor:pointer}@media all and (min-width: 1030px){.error{width:80%}}@media all and (min-width: 1240px){.error{position:absolute;width:95%}}header{background-color:#191A1A;padding:15px;box-shadow:-1px 4px 8px -2px rgba(0,0,0,0.75)}header h1{margin:0;color:#fff;font-size:26px}footer{padding:30px 0;text-align:center}footer .copyright{font-size:14px;font-weight:600;letter-spacing:0.5px}footer .copyright i{background:linear-gradient(-90deg, #8d0b0b, #f21111);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone;font-size:16px}.container{padding:0 0 0 15px}@media all and (min-width: 1100px){.container{padding:0}}

